.contactUsFooterWrapper {
    background-image: url("../../assets/icons/anyQuestionBg.svg");
    height: 137px;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-block-start: 25px;
}

.contactUsFooterContainer {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.contactUsFooterWrapperLeft {
    width: 80%;
    display: flex;
    flex-direction: column;
    color: var(--text-white);
}

.contactUsEmailInputContainer {
    width: 416px;
    height: 64px;
    border-radius: 60px;
    background-color: var(--main-bg-color);
    padding-left: 15px;
    cursor: pointer !important;
}

.contactUsEmailInputContainer::before,
.contactUsEmailInputContainer::after {
    border-bottom: 0 !important;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .contactUsFooterWrapper {
        height: auto;
        padding-block: 15px;
        background-size: cover;
    }

    .contactUsFooterContainer {
        flex-direction: column;
        align-items: flex-start;
    }

    .contactUsEmailInputContainer {
        max-width: 100%;
    }
}